.login_logo{
    width: 150px;
    object-fit: contain;
    margin-top: 20px;
    margin-bottom: 20px;
   
}
.login{
    background-color: azure;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.login_container{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 2px solid gray;
  

}
.login_container >h1{
    font-weight: 500;
    margin-bottom: 20px;
}
.login_container >p{
    font-size: 15px;
    margin-top: 12x;
}
.login_container >form >h5{
    
    margin-bottom: 5px;
}

.login_container >form >input{
    
    margin-bottom: 10px;
    height: 30px;
    background-color:white;
    width: 98%;
    
}
.signin{
    background: rgb(218, 231, 25);
    border-radius: 5px;
    width: 100%;
    height: 30px;
    border: 2px solid;
    margin-top: 10px;
    border-color: rgb(29, 29, 27) rgb(71, 71, 60) rgb(37, 37, 34);
}
.register{
    background: rgb(161, 163, 133);
    border-radius: 5px;
    width: 100%;
    height: 30px;
    border: 2px solid;
    margin-top: 10px;
    border-color: rgb(29, 29, 27) rgb(71, 71, 60) rgb(37, 37, 34);
}