.checkout_ad{
    width: 100%;
    height: 200px;
    margin-bottom: 0px;
    border-radius: 5px;
    
}

.checkout_title{
    margin-right: 10px;
    padding: 10px;
    border-bottom: 2px solid rgb(199, 30, 38);
}

.checkout{
    display: flex;
    padding: 20px;
    background-color: rgb(206, 240, 212);
    height: max-content;
   
}
.checkout_right{
    display: flex;
    flex-direction: row;
}