.product_rating{
    display: flex;
    margin-top: -30px;
    color: yellow;
}
.product{
    display: flex;
    flex-direction: column;
    max-height: 400px;
    min-width: 100px;
    background-color: rgb(215, 233, 219);
    z-index: 1;
    padding: 10;
    margin: 3px;
}
.product > img{
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;

}
.product_price{
    
    margin-top: -15px;
    display: flex;
    color: green;
}
.product_info{
    height: 120px;
    margin-bottom: 5px;
}
.product > button{
    background-color: rgb(240, 236, 18);
    border-color: rgb(240, 28, 45) ;
    border: 2px solid rgb(153, 56, 56);
}
button{
    width: 100px;
   display: flex;
   flex-direction: row;
    justify-content: center;
    margin: 5%;
   align-self: center;
   
    align-items: center;
}