
.header{
    background-color: #2c2c41;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
}
.header_logo{
    width: 100px;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 18px;
    object-fit: contain;
}
.header_search{
display: flex;
flex: 1;
}
.header_searchIcon{
background-color: rgb(228, 213, 11);

padding: 4px;

}
.header_searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;
}
.header_nav{
    display: flex;
    justify-content: space-evenly;
}
.header_link{
    color: white;
    text-decoration: none;
}
.header_option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}
.header_optionLineOne{
    font-size: 10px;
}
.header_optionLineTwo{
    font-size: 13px;
    font-weight: 700;
    
}
.header_optionBasket{
display: flex;
align-items: center;
}
.header_BasketCount{
margin-left: 10px;
margin-right: 10px;

}